/*  ,   */
export async function fetchApiFilter(page = 1, obj = {}) {
  const {
    genre: genre = '',
    year: year = '',
    vote_average: vote_average = '',
    language: language = '',
  } = obj;
  const gte = 'vote_average.gte';
  const options = {
    params: {
      api_key: API_KEY,
      page: `${page}`, //    (: 1),   500 .
      with_genres: `${genre}`, //    (: 28)
      primary_release_year: `${year}`, //      (: 2020)
      with_original_language: `${language}`, //    (: ru)
      [gte]: `${vote_average}`, //  
    },
  };

  try {
    const response = await axios(`3/discover/movie?`, options);
    return response.data;
  } catch (error) {
    return error;
  }
}
